}
.footer ul, .footer ul a{
    list-style: none;
    padding-left: 0px !important;
    display:flex;
    flex-direction: column;
    gap: 10px;
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 18px;
