}

.footer h5{
    font-family: 'Poppins';
font-style: normal;
font-weight: 700;
font-size: 21px;
line-height: 124.5%;
color: #080809;
margin-top: 25px;

}

.social-icons svg {
	width: 50px;
	height: 50px !important;
	/* padding: 20px; */
	/* border: 1px solid black; */
	border-radius: 50%;
	padding: 10px;
    background:#fff;
    fill: #000;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    transform:scale(0.9);
}

.social-icons a:hover svg{
fill: #fff;
background: conic-gradient(from 180deg at 50% 50%, #B8D2F1 0deg, #F289AA 60deg, #C68BF0 106.09deg, #D164DA 153.75deg, #C963E8 221.25deg, #BFC2E8 258.75deg, #FFC999 288.75deg, #D0D8C9 315deg, #BAD0F1 334.13deg, #CED8CB 358.97deg, rgba(255, 255, 255, 0) 360deg);
transition: 0.4s;
transform:scale(1.2);
}
.social-icons{
    margin-top:20px
}
.footer h6{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 124.5%;

/* identical to box height, or 25px */
letter-spacing: 0.005em;

/* TEXT CLR */
color: #5E6282;

margin-top:30px;
}
.footer .stores{
display: flex;
gap: 10px;
