.section-category{
    padding-top:100px;
}

.section-category h3{
    font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 27px;
color: #5E6282;
}

.section-category h2{
font-family: 'Volkhov';
font-style: normal;
font-weight: 700;
font-size: 50px;
line-height: 64px;
text-transform: capitalize;
color: #14183E;
}

.section-category h4{
font-family: 'Open Sans';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 27px;
color: #1E1D4C;
margin: 40px 0px;
}

.section-category p{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 26px;
color: #5E6282;
}
.section-category .catImage{
    
    width: 120px;
    height: 120px;
}

.section-category .categoriesBox{
    
    border-radius: 36px;
    padding:30px 60px;
    position:relative;
   
}

.section-category .categoriesBox:hover{
    
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.02), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0151852), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0121481), 0px 20px 13px rgba(0, 0, 0, 0.01), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.00785185), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00481481);
     
}

.section-category .categoriesBox:hover{
    
     background: #fff;
}
.cat-design {
	position: absolute;
	left: -30px;
	z-index: -1;
	bottom: -37px;
    opacity: 0;
}

.section-category .categoriesBox:hover .cat-design{
    opacity: 1;
    transition: 2s;
}