.subscription-box p{
    font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 33px;
line-height: 54px;
color: #5E6282;
}
.subscription-box{
    padding: 120px 220px;
    background: #DFD7F9;
    background-image: url(./Group\ 78.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
opacity: 1;
border-radius: 20px 20px 20px 129px;
margin-bottom: 180px;
position: relative;
}

.input-group {
	width: fit-content !important;
}
.form{
    display:flex;
    justify-content:center;
    margin-top: 60px;
}

.form .btn {
	margin-left: 20px !important;
    background: linear-gradient(180deg, #FF946D 0%, #FF7D68 100%);
border-radius: 10px !important;
font-family: 'Open Sans' !important;
font-style: normal;
font-weight: 600;
font-size: 17px;
line-height: 23px;
color: #FFFFFF;
border: none !important;
padding: 8px 50px !important;

}

.form-control {
	width: 400px !important;
	/* padding: 25px 0px 25px 25px; */
	height: 60px;
    font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
color: #39425D !important;
padding-left: 25px;
border-radius: 10px !important;
}

.form-control::placeholder {
	color: #39425D !important;
    font-weight: 400;
font-size: 14px;
}
.form img {
	
	position: absolute;
	right: -31px;
	top: -35px;
}

.starsBg {
	position: absolute;
	/* top: 0px; */
	right: 0px;
	bottom: -66px;
}
