.testimonial-para{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 32px;
color: #5E6282;
}

.testimonial-person-info{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.testimonial-person-info .testimonial-person-name{
font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 27px;
color: #5E6282;
}
.testimonial-person-info .testimonial-person-designation{
font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 21px;
color: #5E6282;


}

.testimonial-card{
    background: #FFFFFF;
box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.02), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0151852), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0121481), 0px 20px 13px rgba(0, 0, 0, 0.01), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.00785185), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00481481) !important;
border-radius: 10px !important;

    margin: 0px 80px;
    border: none;
}
.testimonial-card .card-body{
    /* margin: 20px 0px; */
    padding: 30px 80px 30px 60px;
}
.testimonial-image {
	position: absolute;
	top: -20px;
	left: -25px;
}

.carousel-inner {
	padding-top: 42px;
}

.carousel-control-prev {
	height: fit-content;
    width: fit-content;
    position: absolute;
	left: 95%;
	top: 46%;
}

.carousel-control-prev::before {
	content: url("./Vector 3.png");
	position: absolute;
	font-size: 12px;
	color: #000;
	height: fit-content;
    transform: rotate(180deg);
}

.carousel-control-next::before {
	content: url("./Vector 3.png");
	position: absolute;
	font-size: 12px;
	color: #000;
	height: fit-content;
}


.carousel-control-next {
	top: 63%;
	right: 3px;
    width: fit-content;
	height: fit-content;
}

.carousel-indicators [data-bs-target] {
	background: #E5E5E5;
	/* padding: 0px !important; */
	border: 1px solid #E5E5E5;
	padding: 4px;
	border-radius: 50%;
	width: 3px;
}

.carousel-indicators{
    left: -89%;
    right:auto;
    gap: 10px;
}
.carousel-indicators .active{
    background: #39425D;
    border-color: #39425D;
}


